<template>
  <div class="logo-generator">
    <!-- 입력 폼 -->
    <textarea v-model="inputText" placeholder="텍스트를 입력하세요"></textarea>

    <!-- 폰트 스타일 설정 -->
    <div>
      <label for="fontSize">글꼴 크기:</label>
      <input type="number" id="fontSize" v-model.number="fontSize" min="12" max="72">
    </div>
    <div>
      <label for="fontColor">글꼴 색상:</label>
      <input type="color" id="fontColor" v-model="fontColor">
    </div>
    <div>
      <label for="fontFamily">글꼴:</label>
      <select id="fontFamily" v-model="fontFamily">
        <option value="Arial, sans-serif">Arial</option>
        <option value="Times New Roman, serif">Times New Roman</option>
        <option value="Courier New, monospace">Courier New</option>
      </select>
    </div>

    <!-- 스킨 선택 -->
    <div>
      <label for="skin">스킨 선택:</label>
      <select id="skin" v-model="selectedSkin" @change="applySkin">
        <option v-for="(skin, index) in skins" :key="index" :value="skin.value">{{ skin.label }}</option>
      </select>
    </div>

    <!-- 로고 미리보기 -->
    <div v-if="inputText">
      <svg ref="logoSVG" xmlns="http://www.w3.org/2000/svg">
        <text :x="textX" :y="textY" text-anchor="middle" dominant-baseline="middle"
              :font-family="fontFamily" :font-size="fontSize" :fill="fontColor">
          {{ inputText }}
        </text>
      </svg>
    </div>

    <!-- 이미지 다운로드 버튼 -->
    <button v-if="inputText" @click="downloadLogo">로고 다운로드</button>
  </div>
</template>

<script>
import html2canvas from 'html2canvas'

export default {
  name: 'LogoGenerator',
  data () {
    return {
      inputText: '',
      fontSize: 36,
      fontColor: '#000000',
      fontFamily: 'Arial, sans-serif',
      textX: 100,
      textY: 50,
      selectedSkin: 'default', // 기본 스킨 선택
      skins: [
        { value: 'default', label: '기본' },
        { value: 'modern', label: '모던' },
        { value: 'vintage', label: '빈티지' }
        // 다른 스킨 옵션을 필요에 따라 추가할 수 있습니다.
      ]
    }
  },
  methods: {
    applySkin () {
      switch (this.selectedSkin) {
        case 'default':
          this.fontColor = '#000000'
          this.fontFamily = 'Arial, sans-serif'
          this.fontSize = 36
          break
        case 'modern':
          this.fontColor = '#265BFF'
          this.fontFamily = 'Helvetica, Arial, sans-serif'
          this.fontSize = 42
          break
        case 'vintage':
          this.fontColor = '#FF6347'
          this.fontFamily = 'Georgia, serif'
          this.fontSize = 48
          break
        default:
          break
      }
    },
    downloadLogo () {
      const svgElement = this.$refs.logoSVG

      html2canvas(svgElement)
        .then(canvas => {
          const dataUrl = canvas.toDataURL('image/png')

          const link = document.createElement('a')
          link.href = dataUrl
          link.download = 'logo.png'
          document.body.appendChild(link)
          link.click()
          document.body.removeChild(link)
        })
        .catch(error => {
          console.error('로고 다운로드 오류:', error)
        })
    }
  }
}
</script>

<style scoped>
.logo-generator {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  font-family: 'Arial', sans-serif;
}

textarea {
  width: 300px;
  height: 100px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

input, select {
  margin: 5px;
  padding: 5px;
}

svg {
  border: 1px solid #ccc;
  margin-top: 20px;
}

button {
  padding: 10px 20px;
  margin-top: 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}
</style>
